import * as Yup from "yup";
export const ValidationSchemaForModal = () => {
  return Yup.object().shape({
    message_to_landlord: Yup.string().required("Required"),

    proposed_move_date: Yup.string().required("Required"),
    credit_card_details: Yup.string().required("Required"),
    payment_request: Yup.boolean().oneOf([true], "Required"),
    direct_rent_term: Yup.boolean().oneOf([true], "Required"),
    tenants_form: Yup.array().of(
      Yup.object().shape({
        tenant_first_name: Yup.string().required("Required"),
        tenant_sur_name: Yup.string().required("Required"),
        tenant_phone_number: Yup.string().required("Required"),
        tenant_email: Yup.string().required("Required"),
      })
    ),
  });
};

export default ValidationSchemaForModal;
