import { Get } from "../client/REST";
import API from "./BaseAPI";

const uri = "tenants";
const delete_uri = "delete_tenants";
const search_uri = "search";
const multiple_ids = "tenant_ids";
const object_key = "tenant";

const BaseAPI = new API({
  uri,
  delete_uri,
  search_uri,
  multiple_ids,
  object_key,
});

export default {
  ...BaseAPI,
  Get: ({ params }) => Get(`/${uri}`, params),
};
