import React from 'react';
import BaseIndexPage from "components/HOC/BaseIndexPage";
import Modal from "components/global-components/Components/Modal";
import { useState } from "react";
import { connect } from "react-redux";
import StepWizard from "react-step-wizard";
import { Container } from "reactstrap";
import { create, get, getList } from "state/TenantCreation/Epic";
import { selectItem, selectList } from "state/TenantCreation/Selectors";
import { CheckoutModalHeader } from "./Components/CheckOutModal/index";
import Complete from "./Components/Complete";
import Nav from "./Components/Nav";
import {
  PaymentModalFooter,
  PaymentModalHeader,
} from "./Components/PaymentModal/index";
import CheckOutModalBody from "./Form/CheckoutModalBody";
import ModalBody from "./Form/PaymentModalBody";
import DigitalContract from "./Tabs/DigitalContract";
import InitiateTenant from "./Tabs/InitiateTenant";
import Referencing from "./Tabs/Referencing";
import SecurityDeposit from "./Tabs/SecurityDeposit";

let checkoutModalSize = {
  minWidth: "30%",
  zIndex: "99999 !important",
};

let sizeofmodal = {
  minWidth: "45%",
  zIndex: "99999 !important",
};
const TenantCreation = (props) => {
  const [isModalVisible, setisModalVisible] = useState(false);
  const [isaModalVisible, setisaModalVisible] = useState(false);

  const toggleModal = () => {
    setisModalVisible(!isModalVisible);
  };
  const checkoutToggleModal = () => {
    setisaModalVisible(!isaModalVisible);
  };
  return (
    <div className="mg-top-90 py-3 hide-overflow">
      <Container>
        <StepWizard isHashEnabled nav={<Nav />}>
          <InitiateTenant toggleModal={toggleModal} {...props} />
          <Referencing toggleModal={checkoutToggleModal} {...props} />
          <DigitalContract {...props} />
          <SecurityDeposit {...props} />
          <Complete />
        </StepWizard>

        <Modal
          isModalVisible={isModalVisible}
          toggle={() => toggleModal()}
          header={<PaymentModalHeader />}
          footer={<PaymentModalFooter />}
          style={sizeofmodal}
        >
          <ModalBody onRequestVerification={() => toggleModal()} />
        </Modal>

        <Modal
          isModalVisible={isaModalVisible}
          toggle={() => checkoutToggleModal()}
          header={<CheckoutModalHeader />}
          style={checkoutModalSize}
        >
          <CheckOutModalBody
            onRequestVerification={() => checkoutToggleModal()}
          />
        </Modal>
      </Container>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    noHeader: true,
    noBanner: true,
    list: selectList(state),
    item: selectItem(state),
  };
};

const mapDispatchToProps = (dispatch) => ({
  get: (params) => dispatch(get(params)),
  getList: (params) => dispatch(getList(params)),
  create: (params) => dispatch(create(params)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(BaseIndexPage(TenantCreation));
