import {
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  Row,
  Col,
} from "reactstrap";
import React from "react";
import size from "lodash/size";
import isNil from "lodash/isNil";
let modalstyle = {
  minWidth: "70%",
  zIndex: "99999 !important",
  height: "100%",
  top: "20px !important",
};
export default function PrintModal({ toggle, isOpen, pdf }) {
  const contractPdf = () => {
    return (
      size(pdf) > 0 && (
        <>
          {!isNil(pdf[0].src) && (
            <embed
              type="application/pdf"
              width="100%"
              height="600px"
              src={pdf[0].src.base64}
            />
          )}
        </>
      )
    );
  };

  return (
    <Modal
      className="uploadable-pdf"
      centered
      toggle={toggle}
      isOpen={isOpen}
      style={modalstyle}
    >
      <ModalHeader toggle={toggle}>
        <h4>Uploaded Contract</h4>
      </ModalHeader>{" "}
      <ModalBody>
        <Row className="px-3">
          <Col md="12">{contractPdf()}</Col>
        </Row>
      </ModalBody>
      <ModalFooter></ModalFooter>
    </Modal>
  );
}
