import { FieldArray } from "formik";
import React from "react";
import {
  TableBody,
  TableHeader,
  TableWrapper,
} from "components/global-components/Components/Tables/ReactStrapTableWrapper";
import {
  tenants_form_initial_values,
  tenats_form_array_name,
} from "./InitialValues.json";
import TenantFormArrayFields from "./TenantFormArrayFields";

export const TenantFormArray = ({ effectiveDateIndex, isEdit }) => {
  const headerColumns = [
    {
      title: "Full Name",
    },
    {
      title: "Sur Name",
    },
    {
      title: "Email",
    },
    {
      title: "Phone Number",
    },
    {
      title: "Edit/Remove",
    },
  ];

  return (
    <FieldArray
      name={tenats_form_array_name}
      render={({ form: { values }, push, replace, remove }) => {
        const { tenants_form: list } = values;

        return (
          <TableWrapper>
            <TableHeader
              header={headerColumns}
              onAdd={() => push(tenants_form_initial_values)}
            />
            <TableBody>
              {list.map((row, index) => (
                <TenantFormArrayFields
                  key={index}
                  index={index}
                  row={row}
                  onRemove={() => remove(index)}
                />
              ))}
            </TableBody>
          </TableWrapper>
        );
      }}
    />
  );
};

export default TenantFormArray;
