import * as Yup from "yup";
export const ValidationSchema = () => {
  return Yup.object().shape({
    card_number: Yup.string().required("Required"),
    card_expiry_date: Yup.date().required("Required"),
    post_code: Yup.string().required("Required"),
    card_cvc: Yup.string().required("Required"),
  });
};
