import React, { useState } from "react";
import { Table } from "reactstrap";
import cx from "classnames";
import {
  PlusIconButton,
  MinusIconButton,
  UndoIconButton,
} from "../Buttons/index";
import orderBy from "lodash/orderBy";
import isEmpty from "lodash/isEmpty";

export const TableHeader = ({
  onAdd,
  isDisableAddBtn,
  header,
  isAddVisible,
  isSequenceVisible = true,
  name,
  values,
  setFieldValue,
}) => {
  const [sortByColumn, setSortByColumn] = useState({
    asc: false,
    key: "",
  });

  const handleOnClick = (key, type) =>
    isEmpty(name) && isEmpty(values) && isEmpty(setFieldValue)
      ? () => {}
      : () => {
          let sorted_data;
          let sortDirection;
          sortByColumn.key === key
            ? sortByColumn.asc
              ? (sortDirection = "desc")
              : (sortDirection = "asc")
            : (sortDirection = "asc");

          sorted_data = orderBy(
            values,
            type === "number"
              ? function (e) {
                  return parseFloat(e[key]);
                }
              : key,
            sortDirection
          );
          setSortByColumn({
            asc: sortDirection === "asc" ? true : false,
            key: key,
          });
          setFieldValue(name, sorted_data);
        };

  return (
    <thead>
      <tr>
        {isSequenceVisible && (
          <th className="text-center align-middle" style={{ width: 25 }}>
            #
          </th>
        )}
        {header.map(({ title, className, style, key, type }, index) => (
          <th
            className={`${className} pointer ${
              sortByColumn.key === key
                ? sortByColumn.asc
                  ? "asc"
                  : "desc"
                : ""
            }`}
            style={style}
            onClick={handleOnClick(key, type)}
            key={index}
          >
            {title}
          </th>
        ))}
        {isAddVisible !== false && (
          <th style={{ width: 10 }} className="text-center align-middle">
            <PlusIconButton onClick={onAdd} disabled={isDisableAddBtn} />
          </th>
        )}
      </tr>
    </thead>
  );
};

export const TableWrapper = ({ children, className }) => {
  return (
    <Table
      bordered
      responsive
      size="sm"
      className={["sis_sm_table mb-0 primary-table", className]}
    >
      {children}
    </Table>
  );
};

export const TableRow = ({
  rows,
  index,
  onRemove,
  isRemoveVisible,
  isUndoVisible,
  isSequenceVisible = true,
  onUndo,
  isShow = true,
  className,
}) => {
  if (isShow === false) return null;
  return (
    <tr className={cx(isUndoVisible && "tr-disable bg-gainsboro")}>
      {isSequenceVisible && (
        <td className="text-center align-middle">{index + 1}</td>
      )}
      {rows}
      {isRemoveVisible !== false && (
        <td className="text-center align-middle">
          <span>
            <MinusIconButton onClick={() => onRemove(index)} />
          </span>
        </td>
      )}

      {isUndoVisible === true && (
        <td className="text-center align-middle">
          <UndoIconButton onClick={onUndo} />
        </td>
      )}
    </tr>
  );
};

export const TableBody = ({ children }) => {
  return <tbody>{children}</tbody>;
};
