import React from "react";
import Carousel from "@brainhubeu/react-carousel";
import "@brainhubeu/react-carousel/lib/style.css";

export default function ImageCarousel() {
  let publicUrl = `${process.env.PUBLIC_URL}/`;
  let imgalt = "images";
  const images = [
    {
      original: publicUrl + "/assets/img/news/19.png",
      thumbnail: publicUrl + "/assets/img/thumbnail.jpg",
      description: "Slides first",
    },
    {
      original: publicUrl + "/assets/img/news/19.png",
      thumbnail: publicUrl + "/assets/img/thumbnail.jpg",
      description: "Slide second",
    },
    {
      original: publicUrl + "/assets/img/news/19.png",
      thumbnail: publicUrl + "/assets/img/thumbnail.jpg",
    },
  ];
  return (
    <Carousel
      arrows
      slidesPerPage={4}
      slidesPerScroll={1}
      infinite
      offset={50}
      itemWidth={280}
    >
      {images.map((img) => (
        <img src={img.original} alt={imgalt} />
      ))}
    </Carousel>
  );
}
