import * as Yup from "yup";
export const ValidationSchemaForModal = () => {
  return Yup.object().shape({
    propery: Yup.string().required("Required"),

    card_number: Yup.string().required("Required"),
    cvc_number: Yup.string().required("Required"),
    card_expiry_date: Yup.string().required("Required"),
    address_line_1: Yup.string().required("Required"),
    post_code: Yup.string().required("Required"),
    monthly_rent: Yup.string().required("Required"),
    tenants_form: Yup.array().of(
      Yup.object().shape({
        tenant_sur_name: Yup.string().required("Required"),
        tenant_last_name: Yup.string().required("Required"),
        tenant_phone_number: Yup.string().required("Required"),
        tenant_email: Yup.string().required("Required"),
        tenant_of_guarantor: Yup.string().required("Required"),
      })
    ),
  });
};

export default ValidationSchemaForModal;
