import { Field } from "formik";
import React from "react";
import { Link } from "react-router-dom";
import { ReactStrapFormGroupInput } from "../../../components/global-components/Components/Form";
import { TableRow } from "../../../components/global-components/Components/Tables/ReactStrapTableWrapper";
import { tenats_form_array_name } from "./InitialValues.json";

const TenantFormArrayFields = ({ index, onRemove }) => {
  const genName = (name) => `${tenats_form_array_name}[${index}].${name}`;

  return (
    <TableRow
      index={index}
      onRemove={onRemove}
      rows={
        <>
          <td>
            <Field
              name={genName("tenant_first_name")}
              component={ReactStrapFormGroupInput}
            />
          </td>
          <td>
            <Field
              name={genName("tenant_sur_name")}
              component={ReactStrapFormGroupInput}
            />
          </td>
          <td>
            <Field
              name={genName("tenant_email")}
              component={ReactStrapFormGroupInput}
            />
          </td>
          <td>
            <Field
              name={genName("tenant_phone_number")}
              component={ReactStrapFormGroupInput}
            />
          </td>
          <td>
            <Link
              className=" rounded p-1 bg-danger text-white"
              onClick={onRemove(index)}
            >
              <i className="fa fa-trash" /> Remove
            </Link>
          </td>
        </>
      }
    />
  );
};

export default TenantFormArrayFields;
