import { Field } from "formik";
import parse from "html-react-parser";
import isNil from "lodash/isNil";
import size from "lodash/size";
import React, { useState } from "react";
import Files from "react-butterfiles";
import { connect } from "react-redux";
import {
  Button,
  Col,
  Form,
  FormGroup,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Table,
} from "reactstrap";
import {
  ReactStrapFormGroupInput,
  ReactStrapTextEditor,
} from "../../../../components/global-components/Components/Form";
import { FormikHOC } from "../../../../components/global-components/Components/ReactStrapFormik";
import DigitalSignatureIcon from "../DigitalSignatureIcon";
import PrintModal from "../PrintModal";
// import initialValues from "./InitialValues.json";
import ValidationSchema from "./ValidationSchema";
let modalstyle = {
  minWidth: "70%",
};
const DigitalContract = (props) => {
  const [pdftoggle, setPdftoggle] = useState(false);
  const [role, setrole] = useState("");
  const [textEditorData, settextEditorData] = useState("");
  const [toogle, settoogle] = useState(false);
  const [ButtonDisabled, setButtonDisabled] = useState(false);
  const [errors, seterrors] = useState([]);
  const [contractFIle, setContractFile] = useState([]);
  const [customContract, setcustomContract] = useState(false);
  const [defaultContract, setdefaultContract] = useState(false);

  const [contractDetail, setcontractDetail] = useState([
    {
      id: Number(new Date()),
      full_name: " ",
      address: " ",
      email: " ",
      phone_number: " ",
      property_address: " ",
      duration_of_lease: " ",
    },
  ]);
  let List = contractDetail;

  const uploadedContractFIle = (files) => {
    let uploadedfile = contractFIle;
    uploadedfile.push(files[0]);
    setContractFile(uploadedfile);
  };

  const AddInListHandler = () => {
    List.push({
      id: Number(new Date()),
      full_name: " ",
      address: " ",
      email: " ",
      phone_number: " ",
      property_address: " ",
      duration_of_lease: " ",
    });
    setcontractDetail(List);
    setButtonDisabled(true);
  };

  const showDefaultContract = (e) => {
    e.preventDefault();
    setdefaultContract(true);
    setcustomContract(false);
  };

  const showCustomContract = (e) => {
    e.preventDefault();
    setcustomContract(true);
    setdefaultContract(false);
  };

  const texteditorHandler = (data) => {
    settextEditorData(data);
  };

  const userDataHandler = (e, index) => {
    e.preventDefault();
    setButtonDisabled(false);
    const { name, value } = e.target;
    let bItems = [...List];

    bItems[index][name] = value;
    setcontractDetail(bItems);
  };

  const addContractData = (e, role) => {
    setrole(role);
    e.preventDefault();
    if (isNil(props.errors)) {
      settoogle(!toogle);
    } else {
      alert(JSON.stringify(props.errors));
    }
  };
  const deleteTenant = (e, index) => {
    e.preventDefault();
    const bList = contractDetail.filter((data, i) => i !== index);

    setcontractDetail(bList);
  };

  const printOrder = () => {
    var divContents = document.getElementById("modal-body").innerHTML;
    var newWindow = window.open("", "", "height=700, width=1000");
    newWindow.document.write("<html>");
    newWindow.document.write("<body > ");
    newWindow.document.write(divContents);
    newWindow.document.write("</body></html>");
    newWindow.document.close();
    newWindow.print();
  };

  const tableforUser = () => (
    <Table bordered hover className="table-responsive-lg">
      <thead>
        <tr>
          <th>Signature</th>
          <th>Name</th>
          <th>Address</th>
          <th>Email</th>
          <th>phoneNo</th>
          <th>propertyAddress</th>
          <th>durationofLease</th>
        </tr>
      </thead>
      <tbody>
        {contractDetail.map((contract) => (
          <tr key={contract.id}>
            <td className="signature">
              {role === "tenant" ? contract.full_name : ""}
            </td>
            <td>{contract.full_name}</td>
            <td>{contract.email}</td>
            <td>{contract.address}</td>
            <td>{contract.property_address}</td>
            <td>{contract.phone_number}</td>
            <td>{contract.duration_of_lease}</td>
          </tr>
        ))}
      </tbody>
    </Table>
  );

  return (
    <Row>
      <Modal
        centered
        toggle={() => settoogle(!toogle)}
        className="printable-pdf"
        isOpen={toogle}
        style={modalstyle}
      >
        <ModalHeader toggle={() => settoogle(!toogle)}>
          <h4>Contract</h4>
        </ModalHeader>
        <ModalBody id="modal-body">
          <Row className="px-3">
            <Col md="12">
              <h4 className="text-dark mt-3">Contract Statement </h4>
              <div> {parse(textEditorData)}</div>
              <h4 className="text-dark mt-3">Contract Digitally signed by </h4>
              <p>
                <em>
                  Here is the list of people set out to sign the contract, and
                  signatures where they have been collected.
                  <br />
                  Where Signatures have been collected dates and time are
                  displayed in Coordianated Universal time (UTC)
                </em>
              </p>
              <h4 className="text-dark mt-3">The Tenants </h4>
              {tableforUser()}
              <h4 className="text-dark mt-3">The landlord </h4>
              <Table bordered hover className="table-responsive-lg">
                <thead>
                  <tr>
                    <th>Signatures</th>
                    <th>Printed Name</th>
                    <th>Date signed</th>
                    <th>Email (verified)</th>
                    <th>IP Address</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="signature">Olga shandya</td>
                    <td>Olga shandya</td>
                    <td>27/11/2020 19:40:33</td>
                    <td>naqibullah@gmail.com</td>
                    <td> 92.12.30.192</td>
                  </tr>
                </tbody>
              </Table>
            </Col>
          </Row>
        </ModalBody>
        <ModalFooter>
          <Button
            id="printButton"
            className="float-right mr-3 d-inlne-block"
            color="primary"
            onClick={printOrder}
          >
            print
          </Button>
        </ModalFooter>
      </Modal>
      <Col md="12" className="mb-4">
        <h4 className="sub-title text-center my-3 text-dark">
          Digital Contract Signing
        </h4>
        <p>
          Contract between Landlord and Tenant . Landlord can give his own
          contract document or default contract based on his information to
          tenants. Below are two option to select
        </p>
      </Col>

      <br />
      <Col sm="6" className="mt-1">
        {!defaultContract && (
          <button
            className="btn btn-blue btn-block"
            onClick={showDefaultContract}
          >
            <i className="fas fa-file-signature mr-2"></i>
            Default Contract
          </button>
        )}
      </Col>
      <Col sm="6" className="mt-1">
        {!customContract && (
          <button
            className="btn btn-blue bg-secondary btn-block"
            color="secondary"
            onClick={showCustomContract}
          >
            <i className="fas fa-file-signature mr-2"></i>
            Custom Contract
          </button>
        )}
      </Col>

      <Col md="12">
        {" "}
        {defaultContract && (
          <>
            <div className="text-center my-3">
              <DigitalSignatureIcon />
            </div>

            <Form className="TenantDigitalInformation">
              <Row>
                <Col md="12">
                  <Field
                    onChange={texteditorHandler}
                    name="statement_of_contract"
                    placeholder={<b>Statement of Contract</b>}
                    label="Statement of Contract"
                    component={ReactStrapTextEditor}
                  />
                </Col>
                {List.map((data, index) => (
                  <>
                    <Col md="11" className="mt-2">
                      {" "}
                      <Row>
                        <Col md="6">
                          <Field
                            value={data.Name}
                            name="full_name"
                            type="text"
                            placeholder="Name"
                            onChange={(e) => userDataHandler(e, index)}
                            label={<b>Name</b>}
                            component={ReactStrapFormGroupInput}
                          />
                          {/* 
                          <TextInput
                            value={data.Name}
                            name="Name"
                            type="text"
                            placeholder="Name"
                            onChange={(e) => userDataHandler(e, index)}
                            label={<b>Name</b>}
                          /> */}
                        </Col>
                        <Col md="6">
                          <Field
                            value={data.Address}
                            onChange={(e) => userDataHandler(e, index)}
                            name="address"
                            type="text"
                            placeholder="Address"
                            label={<b>Address</b>}
                            component={ReactStrapFormGroupInput}
                          />
                          {/* <TextInput
                            value={data.Address}
                            onChange={(e) => userDataHandler(e, index)}
                            name="Address"
                            type="text"
                            placeholder="Address"
                            label={<b>Address</b>}
                          /> */}
                        </Col>
                        <Col md="6">
                          <Field
                            value={data.Email}
                            onChange={(e) => userDataHandler(e, index)}
                            name="email"
                            type="email"
                            placeholder="Email"
                            label={<b>Email</b>}
                            component={ReactStrapFormGroupInput}
                          />
                          {/* <TextInput
                            value={data.Email}
                            onChange={(e) => userDataHandler(e, index)}
                            name="Email"
                            type="email"
                            placeholder="Email"
                            label={<b>Email</b>}
                          /> */}
                        </Col>
                        <Col md="6">
                          <Field
                            value={data.phoneNo}
                            onChange={(e) => userDataHandler(e, index)}
                            name="phone_number"
                            placeholder="Phone Number"
                            label={<b>Phone Nunber</b>}
                            component={ReactStrapFormGroupInput}
                          />
                          {/* <NumberInput
                            value={data.phoneNo}
                            onChange={(e) => userDataHandler(e, index)}
                            name="phoneNo"
                            placeholder="Phone Number"
                            label={<b>Phone Nunber</b>}
                          /> */}
                        </Col>
                        <Col md="6">
                          <Field
                            value={data.propertyAddress}
                            onChange={(e) => userDataHandler(e, index)}
                            name="property_address"
                            type="text"
                            placeholder="Property Address"
                            label={<b>Property Address</b>}
                            component={ReactStrapFormGroupInput}
                          />
                          {/* <TextInput
                            value={data.propertyAddress}
                            onChange={(e) => userDataHandler(e, index)}
                            name="propertyAddress"
                            type="text"
                            placeholder="Property Address"
                            label={<b>Property Address</b>}
                          /> */}
                        </Col>
                        <Col md="6">
                          <Field
                            value={data.durationofLease}
                            onChange={(e) => userDataHandler(e, index)}
                            name="duration_of_lease"
                            type="text"
                            placeholder="Duration of Lease"
                            label={<b>Duration of Lease</b>}
                            component={ReactStrapFormGroupInput}
                          />
                          {/* <TextInput
                            value={data.durationofLease}
                            onChange={(e) => userDataHandler(e, index)}
                            name="durationofLease"
                            type="text"
                            placeholder="Duration of Lease"
                            label={<b>Duration of Lease</b>}
                          /> */}
                        </Col>
                      </Row>
                    </Col>
                    <Col md="1" className="mt-2">
                      <button
                        className="  btn-sm btn btn-danger text-white"
                        onClick={(e) => deleteTenant(e, index)}
                      >
                        <i className="fas fa-trash-alt"></i>
                      </button>
                    </Col>
                    <hr className="w-100 mx-3" />
                  </>
                ))}

                <Col md="3" className="mt-2 ml-3">
                  <button
                    className="btn btn-blue"
                    type="submit"
                    onClick={(e) => addContractData(e, "landlord")}
                  >
                    Sumbit
                  </button>{" "}
                  <button
                    className="btn btn-blue"
                    onClick={(e) => addContractData(e, "tenant")}
                  >
                    Sign it
                  </button>
                </Col>
                <Col md="6 " className="mt-2 ml-3">
                  <button
                    className="btn btn-blue bg-secondary btn-block "
                    onClick={AddInListHandler}
                    disabled={ButtonDisabled}
                  >
                    <i className="fas fa-user-plus mr-1"></i>
                    Add another Tenant
                  </button>
                </Col>
              </Row>
            </Form>
          </>
        )}
      </Col>
      <Col md="6">
        <Row>
          <Col md="12">
            {customContract && (
              <>
                <div className="text-center my-3">
                  <DigitalSignatureIcon />
                </div>
                <Form>
                  <FormGroup>
                    <Label for="exampleFile">
                      Upload your Contract Document
                    </Label>
                    <br />
                    <Files
                      convertToBase64
                      name="digitalContract"
                      multiple={false}
                      maxSize="2mb"
                      multipleMaxSize="10mb"
                      accept={["application/pdf"]}
                      onSuccess={(files) => uploadedContractFIle(files)}
                      onError={(errors) => seterrors([...errors, errors])}
                    >
                      {({ browseFiles }) => (
                        <>
                          <button
                            onClick={browseFiles}
                            type="file"
                            className="pdf-uploader"
                          >
                            Upload PDF
                          </button>
                          <ol>
                            {size(contractFIle) > 0 && (
                              <li>{contractFIle[0].name}</li>
                            )}
                            {errors.map((error) => (
                              <li key={error.file.name}>
                                {error.file.name} - {error.type}
                              </li>
                            ))}
                          </ol>
                        </>
                      )}
                    </Files>
                  </FormGroup>{" "}
                </Form>
                <PrintModal
                  toggle={() => setPdftoggle(!pdftoggle)}
                  isOpen={pdftoggle}
                  pdf={contractFIle}
                />
                <Row>
                  <Col md="4" sm="6" xs="6">
                    <button
                      type="submit"
                      className="btn btn-blue"
                      onClick={() => setPdftoggle(!pdftoggle)}
                    >
                      Show PDF Modal
                    </button>
                    {"   "}
                  </Col>
                  <Col md="4" sm="6" xs="6">
                    <button type="submit" className="btn btn-blue">
                      Send to Tenant
                    </button>
                  </Col>
                </Row>
              </>
            )}
          </Col>
        </Row>
      </Col>
    </Row>
  );
};

// export default DigitalContract;

const mapStateToProps = (state, props) => ({
  // initialValues: initialValues,
  validationSchema: ValidationSchema,
  isFormikSubmit: false,
});
const mapDispatchToProps = (dispatch) => ({});
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FormikHOC(DigitalContract));
