import React from "react";
import { Col, Row } from "reactstrap";

const Nav = (props) => {
  const dots = [];
  var iconsArray = [
    { text: "Initiate Rent Now", icon: "fas fa-home" },
    { text: "Tenant Referencing", icon: "fas fa-building" },
    { text: "Digital Contract signing", icon: "fas fa-file-contract" },
    { text: "Pay Security deposit and Rent", icon: "far fa-credit-card" },
    { text: "Complete", icon: "fas fa-upload" },
  ];
  for (let i = 1; i <= props.totalSteps; i += 1) {
    const isActive = props.currentStep === i;
    dots.push(
      <Col xs="2" className="tenantsteps" key={`step-${i}`}>
        <div className="single-intro style-two text-center">
          <div className={isActive ? `thumb` : `bgGray`}>
            <div
              onClick={() => {
                props.goToStep(i);
              }}
            >
              <i className={iconsArray[i - 1].icon}></i>
            </div>
          </div>
          <div className="details">
            <h5 className="title">{iconsArray[i - 1].text}</h5>
          </div>
        </div>
      </Col>
    );
  }

  return (
    <>
      <div className="border-bottom mb-4">
        <Row>
          <Col xs="1"></Col>
          {dots}
        </Row>
        <Col xs="1"></Col>
      </div>
    </>
  );
};

export default Nav;
