import React from "react";
import SecurityDeposit from "../Components/SecutityDeposit/SecurityDeposit";
import { initialValues } from "../Components/SecutityDeposit/InitialValues.json";
import isEmpty from "lodash/isEmpty";

const Deposit = ({ item, onSubmitItem, ...props }) => {
  const setInitialValues = () => {
    if (isEmpty(item)) {
      return initialValues;
    } else {
      return item;
    }
  };

  const handleSubmit = (values) => {
    onSubmitItem(values, () => {});
  };
  return (
    <SecurityDeposit
      initialValues={setInitialValues()}
      onSubmit={handleSubmit}
      {...props}
    />
  );
};

export default Deposit;
