import React from "react";
import Referencing from "../Components/TenanatReferencing/Referencing";
import isEmpty from "lodash/isEmpty";
import { initialValues } from "../Components/InitialValues.json";

const TenantReferencing = ({ item, ...props }) => {
  const setInitialValues = () => {
    if (isEmpty(item)) {
      return initialValues;
    } else {
      return item;
    }
  };

  const handleSubmit = () => {};
  return (
    <Referencing
      initialValues={setInitialValues()}
      onSubmit={handleSubmit}
      {...props}
    />
  );
};

export default TenantReferencing;
