import * as Yup from "yup";
export const ValidationSchemaForModal = () => {
  return Yup.object().shape({
    statement_of_contract: Yup.string().required("Required"),
    full_name: Yup.string().required("Required"),
    email: Yup.string().required("Required"),
    address: Yup.string().required("Required"),
    phone_number: Yup.string().required("Required"),
    property_address: Yup.string().required("Required"),
    duration_of_lease: Yup.string().required("Required"),
  });
};

export default ValidationSchemaForModal;
