import React from "react";
import { initialValues } from "./../Components/CheckOutModal/Components/InitialValues.json";
import { CheckoutModalBody } from "./../Components/CheckOutModal/index";
import isEmpty from "lodash/isEmpty";

const ModalBody = ({ item, onSubmitItem, ...props }) => {
  const setInitialValues = () => {
    if (isEmpty(item)) {
      return initialValues;
    } else {
      return item;
    }
  };

  const handleSubmit = (values) => {
    onSubmitItem(values);
  };
  return (
    <CheckoutModalBody
      initialValues={setInitialValues()}
      onSubmit={handleSubmit}
      {...props}
    />
  );
};

export default ModalBody;
